export const toRecord = <T, K extends string, V>(
  array: T[],
  keyMapper: (item: T, index: number) => K,
  valueMapper: (item: T, index: number) => V,
): Record<K, V> => {
  return array.reduce(
    (acc, item, index) => {
      acc[keyMapper(item, index) as K] = valueMapper(item, index) as V;
      return acc;
    },
    {} as Record<K, V>,
  );
};
