import { AddressSelector } from "apps/address-selector";
import { BuildingDashboard } from "apps/building-dashboard";
import { CompanySelector } from "apps/company-selector";
import type { ReactElement } from "react";
import { Route, Routes } from "react-router-dom";
import "./style.css";
import { useBuildingData } from "packages/dataAccess";

export const App = (): ReactElement => {
  const { buildingData, loadingBuildingData } = useBuildingData();

  return (
    <Routes>
      <Route
        path={
          process.env.NODE_ENV === "development" ? "/" : "all-the-companies"
        }
        element={<CompanySelector />}
      />

      <Route
        path={"/:company"}
        element={
          loadingBuildingData ? null : (
            <AddressSelector buildingData={buildingData} />
          )
        }
      />

      <Route
        path={"/:company/:address"}
        element={
          loadingBuildingData ? null : (
            <BuildingDashboard buildingData={buildingData} />
          )
        }
      />
    </Routes>
  );
};
