import { useEffect, useState } from "react";
import type { Dataset } from "../types";
import { Address, AddressUrl, addressItems } from "packages/buildings";
import { toRecord } from "packages/utils";

type Databse = Record<Address, Dataset>;

const fetchBuildingData = async (address: Address): Promise<Dataset> => {
  const response = await fetch(AddressUrl[address]);

  return response.json();
};

let requested = false;

let database: Databse | undefined = undefined;

let queue: {
  [key: string]: (database: Databse) => void;
} = {};

const fetchAllBuildingData = async (key: string): Promise<Databse> => {
  return new Promise(async (res) => {
    if (database) {
      res(database);
    } else {
      if (requested) {
        queue[key] = (database) => {
          res(database);
        };
      } else {
        requested = true;

        let requests = await Promise.all(
          addressItems.map(
            (address) =>
              new Promise<{
                address: string;
                json: Dataset;
              }>(async (res) => {
                res({
                  address,
                  json: await fetchBuildingData(address),
                });
              }),
          ),
        );

        const databaseRecord = toRecord(
          requests,
          (x) => x.address as Address,
          (x) => x.json,
        );

        Object.keys(queue).forEach((key) => queue[key](databaseRecord));

        database = databaseRecord;

        res(databaseRecord);
      }
    }
  });
};

export const useBuildingData = () => {
  const [buildingData, setBuildingData] = useState<Record<string, Dataset>>({});
  const [loadingBuildingData, setLoadingBuildingData] = useState(true);

  useEffect(() => {
    (async () => {
      setBuildingData(await fetchAllBuildingData(Date.now().toString()));

      setLoadingBuildingData(false);
    })();
  }, []);

  return {
    buildingData,
    loadingBuildingData,
  };
};
